.App {
  text-align: center;
}

.App-header {
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-main {
  min-height: 90vh;
  height: 90vh;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: center;
  flex-wrap: nowrap;
  min-width: 600px;
  width: 100vw;
}

.App-home{
  position: absolute;
  background-image: url('./img/tarus.png');
  background-size: 45%;
  background-position: 80% 100%;
  opacity: 0.2;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  z-index: -1;
}

.bgImage {
  background-image: url('./img/tarus.png');
  background-size: 20%;
  background-position: 95% 50%;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
}

.App-player {
  background-image: linear-gradient(360deg, #d8d8d8 10%, white 65%);
  background-repeat: no-repeat;
}

.App-playstyle {
  background-image: linear-gradient(#f0f0f0, white);
  background-repeat: no-repeat;
}

.App-gearsets {
  background-image: linear-gradient(#f0f0f0, white);
  background-repeat: no-repeat;
}

.App-data {
  background-image: linear-gradient(#e2eff1, white);
  background-repeat: no-repeat;
}

.App-link {
  color: #61dafb;
}

.slick-arrow {
  font-size: 24px;
  z-index: 10
}

.slick-prev {
  position: fixed;
  top: 50vh;
  left: 2vw;
}

.slick-prev:before {
  color: #0C0C0C !important;
}

.slick-next {
  position: fixed;
  top: 50vh;
  right: 2vw;
}

.slick-next:before {
  color: #0C0C0C !important;
}